window.addEventListener('load', async () => {
	console.log('WINDOW LOADED');
	const appContainer = getElt('#app');
	const components = await window.Loader.getComponents();

	// to notify player that the template is ready call this:
	window.Loader.ready();

	// to wait for template start (autoPlay is true or player issues play command) use this:
	window.Loader.isStarted().then(() => {
		console.log(components);

		if (!appContainer) return;

		const preElt = document.createElement('pre');
		appContainer.appendChild(preElt);
		preElt.textContent = JSON.stringify(components, null, '  ');
	});
});

function getElt(selector: string) {
	return document.querySelector(selector);
}
